.container {
  font-size: 1.6em;
  font-family: Arial, Helvetica, sans-serif;
  display: block;
  position: relative;
  top: 2%;
  left: 10%;
  width: 80%;
  height: 100%;
}
.form {
  width: 100%;
  border: 1px solid gray;
  border-radius: 10px;
  background-color: #e8e8e8;
  height: 20vh;
  margin-top: 2%;
}
.label-preview {
  float: left;
  margin-left: 2%;
}
.input-box {
  display: flex;
  margin-top: 5%;
  justify-content: space-around;
  text-align: left;
}
.button-change-inner {
  margin-top: 5%;
}
.submit {
  margin-top: 5%;
  font-size: 0.8em;
}
.box {
  display: block;
  width: 100%;
  margin-top: 1%;
}
.box-header {
  border: 1px solid gray;
  border-radius: 10px;
  background-color: #e8e8e8;
}
.form-inner {
  border: 1px solid gray;
  border-radius: 10px;
}
.label-preview-inner {
  float: left;
  margin-left: 2%;
}
.dis-text {
  margin-top: 1%;
}
.title {
  position: fixed;
  bottom: 0%;
  width: 100%;
  background-color: black;
  color: white;
  font-size: 1em;
  margin: 0;
  border: 0;
  text-align: center;
}
.input-box-inner {
  display: flex;
  justify-content: space-around;
  text-align: left;
  margin-top: 1%;
  margin-bottom: 1%;
}
.inner-buttons {
  margin-left: 80%;
}
.font-size {
  font-size: 0.8em;
}
@media only screen and (max-width: 600px) {
  .container {
    font-size: 1em;
    align-items: center;
  }
  .form {
    height: 40vh;
    margin-top: 2%;
  }
  .input-box {
    display: block;
    margin-top: 15%;
    margin-left: 4%;
  }
  .box {
    width: 100%;
    margin-top: 5%;
  }
  .title {
    font-size: 0.8em;
  }
  .label-preview-inner {
    float: none;
  }
  .input-box-inner {
    display: block;
    margin-top: 6%;
    margin-left: 4%;
    margin-bottom: 4%;
  }
  .inner-buttons {
    margin-left: 10%;
  }
  .font-size {
    font-size: 1em;
  }
  .submit {
    font-size: 1em;
  }
}

@media only screen and (min-width: 700px) and (max-width: 1024px) {
  .container {
    width: 95%;
    left: 2%;
  }
  .form {
    height: 12vh;
    margin-top: 2%;
  }
  .box {
    margin-top: 2%;
    width: 100%;
  }
  .button-change-inner {
    margin-top: 5%;
  }
  .submit {
    margin-top: 5%;
  }
  .inner-buttons {
    margin-left: 70%;
  }
  .font-size {
    font-size: 1em;
  }
  .submit {
    font-size: 1em;
  }
}
@media only screen and (max-width: 360px) {
  .container {
    font-size: 0.8em;
  }
  .form {
    height: 40vh;
    margin-top: 2%;
  }
  .input-box {
    display: block;
    margin-top: 15%;
    margin-left: 4%;
  }
  .label-preview-inner {
    float: none;
  }
  .inner-buttons {
    margin-left: 10%;
  }
}
